@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  display: inline-block;
  margin: 0;
  padding: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}

.title-lines {
  font-size: 24px;
  line-height: 24px;
}

.title {
  font-size: 36px;
  line-height: 40px;
}

.page-title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.container {
  padding: 0 20px;
}

.main-header {
  background-color: #ffffff;
}

.main-header__container {
  padding: 0;
}

.main-nav {
  min-height: 65px;
  padding-top: 13px;
}

.nav__link {
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.intro__container {
  padding: 0;
}

.intro__content {
  text-align: center;
}

.intro--home .intro__content {
  color: #ffffff;
  background-color: #5eaa2f;
  background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/intro-background.jpg");
  background-repeat: no-repeat;
  background-position: center;
}

.intro__text {
  line-height: 14px;
  color: #ffffff;
  text-transform: uppercase;
}

.intro__img {
  width: 280px;
  height: 270px;
  margin: 0 auto;
}

.intro__img img {
  display: block;
  width: 100%;
  height: 100%;
}

.main-content {
  padding: 0;
}

.program__container {
  margin: 0 20px 20px;
  background-color: #f2f2f2;
}

.program__title {
  padding-bottom: 29px;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}

.program__content {
  padding: 0;
  padding-bottom: 23px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.program__link {
  padding-right: 48px;
  font-size: 16px;
  line-height: 16px;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.advantages__title {
  padding-bottom: 40px;
}

.advantages__item {
  padding-bottom: 26px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.example {
  background-color: #f2f2f2;
}

.example__content {
  padding-bottom: 26px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.example__term {
  font-size: 12px;
  line-height: 12px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.example__definition {
  font-size: 16px;
  line-height: 16px;
}

.example__prise {
  line-height: 20px;
  text-transform: uppercase;
}

.slider {
  padding: 0;
}

.catalog__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
}

.catalog__title-link {
  text-decoration: none;
}

.catalog__description {
  font-size: 12px;
  line-height: 15px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.catalog__description-item dd {
  margin: 0;
}

.more__item-title {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #444444;
  text-transform: uppercase;
}

.more__pack {
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.more__price {
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.more__offer {
  padding: 172px 20px 26px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Lato", "Arial", sans-serif;
  color: #ffffff;
  background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/more-background.jpg");
  background-repeat: no-repeat;
}

.form__fieldset {
  margin: 0 auto;
  border: none;
}

.form__label {
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
  text-transform: uppercase;
}

.form__input {
  padding: 11px 20px;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #444444;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #e7e7e7;
}

.form__legend {
  margin-bottom: 34px;
}

.control__label {
  padding-left: 15px;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}

.main-footer {
  background-color: #f2f2f2;
}

.map--home .map__container {
  background-color: #ffffff;
}

.map__contact {
  position: relative;
}

.map__title {
  margin: 0;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.map__adreess {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}

.map__iframe-container {
  height: 400px;
}

.map__iframe {
  height: 100%;
}

.map__iframe iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
}

.social {
  padding: 0;
}

.developer {
  text-decoration: none;
}

.developer__name {
  font-size: 16px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
}